@use 'base/variable' as v;
@use 'base/mixins' as m;

.login {
    border: 1px solid red;
    height: 80vh;
    display: flex;
    justify-content: space-evenly;
    margin: 5%;
}

.image-login,
.login-form {
    margin: auto 0;
}

.image-login {
    max-width: 50%;
    display: flex;
    img {
        width: 100%;
        margin: auto;
    }
}