/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  background-color: #fffff0;
  color: #000;
  font-family: "Jura", sans-serif;
  font-size: 1.6rem;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.imagen-small {
  width: 20rem;
}

.ocult {
  display: none;
}

.image {
  width: 20%;
}
.image .image-index {
  width: 100%;
  padding: 1rem;
}

h1, h2, h3 {
  font-family: "Jura", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: #000;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
  font-weight: 700;
}

p {
  font-family: "Titillium Web", sans-serif;
  color: #fffff0;
  font-size: 2rem;
  font-weight: 300;
}

@media (min-width: 768px) {
  .sidebar {
    background-color: #000;
    padding: 2rem;
    max-width: 250px;
  }
  .sidebar h2 {
    color: #fffff0;
    margin: 0;
    text-align: center;
    font-size: 2rem;
    font-weight: 700;
    margin-top: 2rem;
    font-family: "Titillium Web", sans-serif;
  }
  .sidebar .logoAlfa {
    width: 50%;
    margin: 0 auto;
  }
}

@media (min-width: 768px) {
  .sidebar-nav {
    margin-top: 4rem;
  }
  .sidebar-nav a {
    color: #fffff0;
    display: block;
    margin-bottom: 1.5rem;
    padding: 0.5rem;
    transition-property: color;
    transition-duration: 300ms;
    font-size: 1.5rem;
    font-weight: 700;
    font-family: "Titillium Web", sans-serif;
  }
  .sidebar-nav a:hover, .sidebar-nav a:active {
    color: #b69253;
  }
}

.dashboard {
  background-color: #f2f2f2;
}
@media (min-width: 768px) {
  .dashboard {
    display: flex;
    height: 100vh;
  }
}
.dashboard .principal {
  flex: 1;
  overflow: scroll;
}
.dashboard .content-page {
  padding: 2rem;
}

.newCasino {
  display: flex;
  flex-direction: row-reverse;
}
.newCasino a {
  background-color: #000;
  padding: 1.2rem;
  border-radius: 0.5rem;
  color: #fffff0;
  transition-property: color background-color;
  transition-duration: 300ms;
  font-weight: 700;
}
.newCasino a:hover {
  color: #000;
  background-color: #b69253;
}

.AllCasinos h3 {
  font-size: 4.5rem;
}
.AllCasinos .card {
  max-width: 40rem;
}
.AllCasinos .card h5, .AllCasinos .card p {
  color: #000;
}

@media (min-width: 768px) {
  .cardsCasinos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.cardsCasinos img {
  opacity: 0.5;
}
.cardsCasinos .card-img-overlay {
  display: flex;
  flex-direction: row-reverse;
}
.cardsCasinos .card-img-overlay h3 {
  font-size: 2rem;
  font-weight: 700;
  font-family: "Titillium Web", sans-serif;
}
.cardsCasinos .card-title {
  text-align: center;
  font-size: 2rem;
  font-weight: 700;
}
.cardsCasinos .card-footer {
  display: flex;
  justify-content: space-around;
}
.cardsCasinos .card-footer a {
  font-size: 2rem;
  padding: 0.5rem 1.5rem;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
}
.cardsCasinos .card-footer a:hover {
  background-color: #b69253;
}

.newClient {
  display: flex;
  flex-direction: row-reverse;
}
.newClient a {
  background-color: #000;
  padding: 1.2rem;
  border-radius: 0.5rem;
  color: #fffff0;
  transition-property: color background-color;
  transition-duration: 300ms;
  font-weight: 700;
}
.newClient a:hover {
  color: #000;
  background-color: #b69253;
}

.card {
  max-width: 32.5rem;
}
.card h5, .card p {
  color: #000;
}

@media (min-width: 768px) {
  .machinesCasinos {
    display: grid;
    gap: 1;
    grid-template-columns: repeat(3, 1fr);
  }
}
.machinesCasinos img {
  opacity: 0.5;
}
.machinesCasinos .card-img-overlay {
  display: flex;
  flex-direction: row-reverse;
}
.machinesCasinos .card-img-overlay h3 {
  font-size: 2rem;
  font-weight: 700;
  font-family: "Titillium Web", sans-serif;
}
.machinesCasinos .card-title {
  text-align: center;
  font-size: 2rem;
  font-weight: 700;
}
.machinesCasinos .card-footer {
  display: flex;
  justify-content: space-around;
}
.machinesCasinos .card-footer a {
  font-size: 2rem;
  padding: 0.5rem 1.5rem;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
}
.machinesCasinos .card-footer a:hover {
  background-color: #b69253;
}

.form-group {
  margin: 1.5rem 0;
}

.center-form {
  margin: 0 auto;
}
.center-form .file-image {
  width: 50%;
  margin: 5rem;
}

.login {
  border: 1px solid red;
  height: 80vh;
  display: flex;
  justify-content: space-evenly;
  margin: 5%;
}

.image-login,
.login-form {
  margin: auto 0;
}

.image-login {
  max-width: 50%;
  display: flex;
}
.image-login img {
  width: 100%;
  margin: auto;
}